<template>
    <div>
        <navbar></navbar>
        <h2 class="order-title">確認訂單</h2>
        <div class="my-5 row justify-content-center">
            <form class="col-md-6" @submit.prevent="payOrder">
                <table class="table">
                    <thead>
                        <th>商品</th>
                        <th>品名</th>
                        <th>數量</th>
                        <th>單價</th>
                    </thead>
                    <tbody>
                        <tr v-for="item in order.products" :key="item.id">
                        <td class="align-middle">
                            <img :src="item.product.imageUrl" alt="" class="cart-item-img">
                        </td>  
                        <td class="align-middle">{{ item.product.title }}</td>
                        <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
                        <td class="align-middle text-right">NT${{ item.final_total }}</td>
                        </tr>
                    </tbody>
                    <tfoot>
                        <tr>
                        <td colspan="2" class="text-right">總計</td>
                        <td class="text-right">NT${{ order.total }}</td>
                        </tr>
                    </tfoot>
                </table>

                <table class="table">
                    <tbody>
                        <tr>
                        <th width="100">Email</th>
                        <td>{{ order.user.email }}</td>
                        </tr>
                        <tr>
                        <th>姓名</th>
                        <td>{{ order.user.name }}</td>
                        </tr>
                        <tr>
                        <th>收件人電話</th>
                        <td>{{ order.user.tel }}</td>
                        </tr>
                        <tr>
                        <th>收件人地址</th>
                        <td>{{ order.user.address }}</td>
                        </tr>
                        <tr>
                        <th>付款狀態</th>
                        <td>
                            <span v-if="!order.is_paid">尚未付款</span>
                            <span v-else class="text-success">付款完成</span>
                        </td>
                        </tr>
                    </tbody>
                </table>

                <div class="text-right" v-if="order.is_paid === true">
                    <router-link to="/index"><button class="btn btn-info">繼續逛逛</button></router-link>
                </div>
                <div class="text-right" v-if="order.is_paid === false">
                    <button class="btn btn-danger">確認付款去</button>
                </div>
            </form>
            </div>
            <app-footer></app-footer>
    </div>
</template>

<script>
import Navbar from './navbar.vue'
import Footer from './footer.vue'
    export default{
        components: {
            'navbar': Navbar,
            'app-footer': Footer
        },
        data(){
            return {
                orderId: '',
                order: {
                    user: {

                    }
                }
            }
        },
        methods:{
            getOrder(){
                const vm = this;
                const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order/${vm.orderId}`
                this.$http.get(url).then((res) => {
                    vm.order = res.data.order
                    console.log(res)
                })
            },
            payOrder(){
                const vm = this;
                const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/pay/${vm.orderId}`
                this.$http.post(url).then((res) => {
                    if (res.data.success){
                        vm.getOrder()
                    }
                })
            },
        },
        created(){
            this.orderId = this.$route.params.orderId
            this.getOrder();
        }
    }
</script>

<style lang="sass" scoped>
.order-title
    text-align: center
.cart-item-img
    width: 50px
</style>



